#root {
  height: 100%
}

html,
body {
  position: relative;
  height: 100%;
  margin: 0;
}

.golapi-swiper-coverflow {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.golapi-swiper-coverflow img {
  border-radius: 0.5em;
}

.golapi-swiper-fade {
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
}

.golapi-swiper-fade, .golapi-swiper-fade * {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  object-position: center;
}


.golapi-swiper-fade img {
  filter: blur(5px) brightness(0.5) grayscale(0.75);
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 90%;
  height: fit-content;
}

.swiper-slide img {
  display: block;
  width: 100%;
}

@media (min-width: 768px) { 
  .swiper-slide {
    width: 300px;
  }  
}
